import { ArousalCtrlSequence, FeetCtrlSequence, FuturisticPublicCtrlSequence, HandsCtrlSequence, HearingCtrlSequence, ToysCtrlSequence, VisionCtrlSequence, VoiceCtrlSequence } from "../../Control/SequenceCtrl/ItemCmdSequence";
import { CommandTemplate } from "../ICmds";
import { BasicPrerequisites } from "../Prerequistes";

export const ItemCmds: CommandTemplate[] = [
    {
        match: /^((||))|(hearing limitation (off|basic|full))/i,
        prerequisite: BasicPrerequisites,
        run(player, sender, content) {
            HearingCtrlSequence(player, (() => {
                if (content[2] === "" || content[4].toLowerCase() === "off") return "off";
                if (content[2] === "" || content[4].toLowerCase() === "basic") return "base";
                if (content[2] === "" || content[4].toLowerCase() === "full") return "total";
                return "total";
            })())
        }
    },
    {
        match: /^((||))|(visual restrictions (off|basic|full))/i,
        prerequisite: BasicPrerequisites,
        run(player, sender, content) {
            VisionCtrlSequence(player, (() => {
                if (content[2] === "" || content[4].toLowerCase() === "off") return "off";
                if (content[2] === "" || content[4].toLowerCase() === "basic") return "base";
                if (content[2] === "" || content[4].toLowerCase() === "full") return "total";
                return "total";
            })());
        }
    },
    {
        match: /^((||))|(language restrictions (off|basic|full))/i,
        prerequisite: BasicPrerequisites,
        run(player, sender, content) {
            VoiceCtrlSequence(player, (() => {
                if (content[2] === "" || content[4].toLowerCase() === "off") return "off";
                if (content[2] === "" || content[4].toLowerCase() === "basic") return "base";
                if (content[2] === "" || content[4].toLowerCase() === "full") return "total";
                return "total";
            })());
        }
    },
    {
        match: /^((||))|(arm restraint (off|basic|full))/i,
        prerequisite: BasicPrerequisites,
        run(player, sender, content) {
            HandsCtrlSequence(player, (() => {
                if (content[2] === "" || content[4].toLowerCase() === "off") return "off";
                if (content[2] === "" || content[4].toLowerCase() === "basic") return "base";
                if (content[2] === "" || content[4].toLowerCase() === "full") return "total";
                return "total";
            })());
        }
    },
    {
        match: /^((||))|(leg restraint (off|basic|full))/i,
        prerequisite: BasicPrerequisites,
        run(player, sender, content) {
            FeetCtrlSequence(player, (() => {
                if (content[2] === "" || content[4].toLowerCase() === "off") return "off";
                if (content[2] === "" || content[4].toLowerCase() === "basic") return "base";
                if (content[2] === "" || content[4].toLowerCase() === "full") return "total";
                return "total";
            })());
        }
    },
    {
        match: /^((||))|(orgasm lock (off|edge|deny))/i,
        prerequisite: BasicPrerequisites,
        run(player, sender, content) {
            ArousalCtrlSequence(player, (() => {
                if (content[2] === "" || content[4].toLowerCase() === "off") return "off";
                if (content[2] === "" || content[4].toLowerCase() === "edge") return "edge";
                if (content[2] === "" || content[4].toLowerCase() === "deny") return "deny";
                return "off";
            })());
        }
    },
    {
        match: /^((||))|(vibrator (off|on|max))/i,
        prerequisite: BasicPrerequisites,
        run(player, sender, content) {
            ToysCtrlSequence(player, (() => {
                if (content[2] === "" || content[4].toLowerCase() === "off") return "off";
                if (content[2] === "" || content[4].toLowerCase() === "on") return "open";
                if (content[2] === "" || content[4].toLowerCase() === "max") return "max";
                return "max";
            })());
        }
    },
    {
        match: /^((|))|(public operation permissions (close|open))/i,
        prerequisite: BasicPrerequisites,
        run(player, sender, content) {
            FuturisticPublicCtrlSequence(player, (() => {
                if (content[2] === "" || content[4].toLowerCase() === "close") return "close";
                if (content[2] === "" || content[4].toLowerCase() === "open") return "open";
                return "open";
            })());
        }
    },
];
